import React, { useContext, useEffect, useState } from 'react';

import { FaPlay, FaPause } from 'react-icons/fa';

import { StoreContext } from '../store/store';

import { Player, SpikeLeft, SpikeRight, SpikesTop } from './Shapes';

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const Board = () => {
    
    const { high } = useContext(StoreContext);
    const { player } = useContext(StoreContext);
    const { spikes } = useContext(StoreContext);

    const { setHigh } = useContext(StoreContext);
    const { setScore } = useContext(StoreContext);
    const { setSpikes } = useContext(StoreContext);

    /*   *   *   *   *   *   *   *   *   *   */

    const [ side, setSide ] = useState(false);
    const changeSide = () => setSide(prev => !prev);

    const [ stop, setStop ] = useState(true);
    const toggleStop = () => setStop(prev => !prev);


    let score = 0;
    const btn = stop ? 'btn start' : 'btn stop';
    const refresh = 200;

    /*   *   *   *   *   *   *   *   *   *   */

    const [ gameSpikes, setGameSpikes ] = useState([]);

    const addNewSpike = () => {
        const spike = Boolean(Math.floor(Math.random()*2))
        ? <SpikeLeft key={score}/>
        : <SpikeRight key={score}/>

        score++;
        document.querySelector('.curr').textContent = score;

        setGameSpikes(prev => [ spike, ...prev.slice(0, 19) ]);
    }

    /*   *   *   *   *   *   *   *   *   *   */

    const [ collision, setCollision ] = useState(false);

    const getDistance = ( x1, x2, y1, y2 ) => {
        return Math.sqrt( (x1 - x2)**2 + (y1 - y2)**2 );
    }

    const addMove = () => {
        const updatedSpikes = [...spikes];
        
        updatedSpikes.forEach(spike => {
            spike.y += window.innerHeight / (Math.SQRT2*refresh);
        });

        isCollision();

        setSpikes(updatedSpikes);
    }

    const isCollision = () => {
        const collisionDetected = false;
        spikes.forEach(spike => {
            const d = getDistance( spike.x, player.x, spike.y, player.y );
            const r = spike.r + player.r;

            if(d < (r / Math.SQRT2) && (spike.y + player.w/2) < player.y) {
                setCollision(true);
                setStop(true);
            }
        });
    }

    /*   *   *   *   *   *   *   *   *   *   */
    /*

    useEffect(() => {

        if(!stop) {
            document.querySelector('.curr').textContent = score;
            const interval = setInterval(() => addNewSpike(), 1000 * 50 / (score + 50));
            return(() => {clearInterval(interval); setScore(score);});
        }

        if(stop) {
            setSpikes([]);
            setGameSpikes([]);
        }

    }, [ stop, collision ])

    useEffect(() => {

        if(!stop) {
            const interval = setInterval(() => addMove(), 1000 / refresh);
            return(() => clearInterval(interval));
        }

    }, [ stop, spikes ])

    useEffect(() => {

        const r_curr = document.querySelector('.curr');
        const r_high = document.querySelector('.high');

        r_curr.textContent = score;
        r_high.textContent = high;

    }, [ stop, high ])

    */
    /*   *   *   *   *   *   *   *   *   *   */
    
    return(
    <section className='board' onClick={!stop ? changeSide : null}>

        <span className='curr'></span>
        <span className='high'></span>

        <button className={btn} onClick={toggleStop}>
            {stop ? <FaPlay/> : <FaPause/>}
        </button>

        <Player idName='player' side={side}/>

        <SpikesTop/>

        {gameSpikes}

    </section>
)};

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

export default Board;