import React, { createContext, useEffect, useState } from 'react';

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const StoreContext = createContext(null);

const initPlayer = { x: 0, y: 0, w: 0, r: 0, o: 0 };

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const StoreProvider = ({ children }) => {

    const [ score, setScore ] = useState(2);
    const [ high, setHigh ] = useState(0);

    const [ player, setPlayer ] = useState(initPlayer);
    const [ spikes, setSpikes ] = useState([]);

    
    /*   *   *   *   *   *   *   *   *   *   */

    const unwrapCookies = cookies => {
        const highScore = cookies.filter(elem => elem.startsWith('highScore')).pop();
        return +highScore.slice(10);
    }

    /*   *   *   *   *   *   *   *   *   *   */

    const storeValue = {
        high, setHigh,
        score, setScore,
        player, setPlayer,
        spikes, setSpikes,
    };

    /*   *   *   *   *   *   *   *   *   *   */

    /*

    useEffect(() => {

        const prev = unwrapCookies(document.cookie.split(/; * /));
        setHigh(prev);

    }, [])

    useEffect(() => {

        const prev = unwrapCookies(document.cookie.split(/; * /));
        const curr = score;

        if(curr > prev) {
            document.cookie = `highScore=${curr}`;
            setHigh(curr);
        }


    }, [ score ])

    */

    /*   *   *   *   *   *   *   *   *   *   */

    return(
        <StoreContext.Provider value={storeValue}>
            {children}
        </StoreContext.Provider>
)}

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

export default StoreProvider;
export { StoreContext, StoreProvider };