import React, { useContext, useEffect } from 'react';

import { StoreContext } from '../store/store';

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const Player = ({ idName, side }) => {

    const { player, setPlayer } = useContext(StoreContext);

    const translate = side ? player.o - player.r : 0;
    const rotate = side ? 90 : 0;

    const transform = `translateX(${-translate}px) rotate(${-rotate}deg)`;

    /*   *   *   *   *   *   *   *   *   *   */

    useEffect(() => {

        const { y, w, r, o } = player;
        const x = o - translate;

        setPlayer({ x, y, w, r, o });

    }, [side]);

    useEffect(() => {

        const elem = document.querySelector('.player');

        const x = elem.offsetLeft + elem.offsetWidth/2;
        const y = elem.offsetTop + elem.offsetHeight/2;

        const w = elem.offsetWidth;
        const r = w / 2;
        const o = x;

        setPlayer({ x, y, w, r, o });

    }, []);

    /*   *   *   *   *   *   *   *   *   *   */
    
    return(
    <div className='player' id={idName} style={{transform: transform}}><span></span></div>
)};

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const SpikeLeft = () => {

    const { setSpikes } = useContext(StoreContext);

    /*   *   *   *   *   *   *   *   *   *   */

    useEffect(() => {

        const elem = document.querySelector('.spike');

        const x = elem.offsetLeft + elem.offsetWidth/2;
        const y = elem.offsetTop + elem.offsetHeight/2;
        const r = elem.offsetWidth / 2;

        setSpikes(prev => [ { x, y, r }, ...prev.slice(0, 19) ]);

    }, []);

    /*   *   *   *   *   *   *   *   *   *   */
    
    return(
    <div className='spike __left'></div>
)};

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const SpikeRight = () => {

    const { setSpikes } = useContext(StoreContext);

    /*   *   *   *   *   *   *   *   *   *   */

    useEffect(() => {

        const elem = document.querySelector('.spike');

        const x = elem.offsetLeft + elem.offsetWidth/2;
        const y = elem.offsetTop + elem.offsetHeight/2;
        const r = elem.offsetWidth / 2;

        setSpikes(prev => [ { x, y, r }, ...prev.slice(0, 19) ]);

    }, []);

    /*   *   *   *   *   *   *   *   *   *   */
    
    return(
    <div className='spike __right'></div>
)};

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const SpikesTop = () => {

    /*   *   *   *   *   *   *   *   *   *   */
    
    return(
    <div className='spikes-top'>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
        <div className='spike-top'></div>
    </div>
)};

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

export { Player, SpikeLeft, SpikeRight, SpikesTop };